import requestUtil from '@/utils/request'

// 获取商品详情
export const getProDetailApi = (goodsId) => {
  return requestUtil.get('/goods/detail', {
    params: {
      goodsId
    }
  })
}

// 获取评论
export const getCommentsApi = (goodsId) => {
  return requestUtil.get('/comment/listRows', {
    params: {
      goodsId,
      limit: 5
    }
  })
}

// 商品保障服务
export const getGoodsServiceApi = (goodsId) => {
  return requestUtil({
    url: '/goods.service/list',
    params: {
      goodsId
    }
  })
}
