import { Dialog } from 'vant'

export default {
  methods: {
    isUserLogin () {
      if (!this.$store.getters.ifToken) {
        Dialog.confirm({
          message: '您还没有登录',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            this.$router.push({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => {})
        return false
      } else {
        return true
      }
    }
  }
}
